@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
html, body {
  margin: 0;
  height: 100%;
  width: 100%;
  padding:0;
}

body {
  font-family: 'DM Sans', -apple-system, BlinkMacSystemFont, 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #161614;
  font-size: 16px;
  line-height: 1.5;
  
}

section {
  display: block;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
}

.title {
  font-weight: 700;
}

.title-accent {
  border-left: 5px solid #ffdb03;
  padding-left: 10px;
}

.subtitle {
  font-weight: 500;
  color: #5F605B;
}

a {
  text-decoration: none;
  color: #3d3d3b;
  font-weight: 600;
  letter-spacing: 1px;
}

a:hover, .link-main:hover {
  color: #5F605B;
  box-shadow: 0 4px 0px -1px #ffdb03;
}

.nav-link {
  padding: 0;
  margin: .5rem 1rem;
}

.text-second {
  color: #5F605B;
}

.btn-main {
  color: #161614;
  background-color: #ffdb03;
  border-color: #ffdb03;
  border-radius: 0;
}

.btn-main:hover {
  background-color: #f0cc00;
  box-shadow: none;
}
.btn-outline-main {
  color: #161614;
  border-color: none;
  border-left: 3px solid #ffdb03;
  border-radius: 0;
}

.btn-outline-main:hover {
  background-color: #ffdb03;
  border-color: #ffdb03;
  box-shadow: none;
}

.btn-main:focus, .btn-outline-main:focus {
  box-shadow: 0 0 0 0.2rem rgba(255,219,3,.25)
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

